import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api";

class AddressService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/property/address/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  // ------------------------- To get the applicant residential address for DDE ------------------------- //

  getAddressWithoutDocForDde = (loanId: string, applicantUid: string, addressType: string) => {
    const url = `${DEFAULT_PATH}/property/address/search?loan.uid=${loanId}&applicant.uid=${applicantUid}&isActive=true&primaryAddressType=${addressType}`;
    return this.makeGetRequestWithAuth(url);
  };

  // ------------------------- To get a registered  address for DDE corporate ------------------------- //

  getOrgRegisteredAddress = (loanId: string, id: string) => {
    const endPoint = DEFAULT_PATH + `/login/basic/info/corporate/address/${loanId}/${id}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To create a registered  address for DDE corporate ------------------------- //

  createOrgRegisteredAddress = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/login/dde/basic/info/corporate/address`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  // ------------------------- To create a registered  address for DDE corporate ------------------------- //

  updateOrgRegisteredAddress = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/login/dde/basic/info/corporate/address`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };
}

export default new AddressService();
